<template>
	<div id="Dheader">
    <div id="Dselect">
    <span id="Dname">{{ name }}</span>
    <el-select v-model="time" placeholder="请选择" size="mini">
      <el-option
        v-for="item in timeselect"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    </div>
    <div id="Dimg">
    <a><img src="@/assets/icon/icon_add.png" id="icon_add"></a>
    <el-dropdown trigger="click">
      <a><img src="@/assets/icon/icon_set.png" id="icon_set"></a>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>Client Details</el-dropdown-item>
        <el-dropdown-item>Mark Visit</el-dropdown-item>
        <el-dropdown-item>Set Alerts</el-dropdown-item>
        <el-dropdown-item>Add Suggestion</el-dropdown-item>
        <el-dropdown-item>Archive Client</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <!-- <a href=""><img src="@/assets/icon/icon_set.png" id="icon_set"></a>
 -->    </div>
	</div>
</template>

<script>
  import menu from '@/config/menu-config.js'
export default {
  name: 'GetName',
  data() {
    return {
     path: this.$route.path,
     menu: menu,
     name: '',
     Cname: '',
     timeselect: [{
          value: 'option1',
          label: 'Since Last Vist'
        }, {
          value: 'option2',
          label: 'All Time'
        }],
      time: 'option1',
    }
   },
   created() {
    this.Cname = '/' + this.$route.path.split('/')[1]
    for (var i = 0; i < this.menu[0].sub.length; i++) {
        if(this.menu[0].sub[i].componentName == this.Cname) {
          this.name = this.menu[0].sub[i].name
        }
      }   
    },
   watch: {
    '$route' (to, from) {
      for (var i = 0; i < this.menu[0].sub.length; i++) {
        if(this.menu[0].sub[i].componentName == this.$route.path) {
          this.name = this.menu[0].sub[i].name
        }
      }
    }
   },
  }
</script>

<style>
  a {
    cursor: pointer;
  }
  #Dheader {
    margin-top: 40px;
  }
  #Dselect {
    display: inline-block;
    margin-top: 20px;
  }
  #Dname {
    margin-right: 30px;
    font-weight: bold;
    color: #5D5D5D;
    font-size: 24px;
  }
  .el-input__inner {
    font-weight: bold;
    font-size: 13px;
    background-color: #DEDEDE;
  }
  .el-select .el-input .el-select__caret {
    color: #2c3e50;
  }
  .el-select {
    width: 160px;
  }
  #Dimg {
    float: right;
    line-height: 80px;
    display: inline-block;
    margin-right: 20px;
  }
  #icon_add {
    height: 60px;
    vertical-align: middle;
  }
  #icon_set {
    float: right;
    height: 20px;
    margin-right: 56px;
    margin-left: 60px;
    margin-top: 30px;
  }
</style>


